import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'app-misc-input',
  templateUrl: './misc-input.component.html',
  styleUrls: ['./misc-input.component.scss']
})
export class MiscInputComponent implements OnInit, OnChanges {
  @Input() miscGroup: FormGroup;
  constructor() { }

  ngOnInit() {
  }
  ngOnChanges(changes: SimpleChanges): void {
    if (!!changes['miscGroup']) {
      this.miscGroup = changes['miscGroup'].currentValue;
    }
  }
  get title() {return this.miscGroup.get('title'); }
  get description() {return this.miscGroup.get('description'); }

}
