import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { UpdateAppointmentWrapperComponent } from './components/update-appointment-wrapper/update-appointment-wrapper.component';
import { AddAppointmentWrapperComponent } from './components/add-appointment-wrapper/add-appointment-wrapper.component';

const routes: Routes = [
  {
      path: 'add',
      component: AddAppointmentWrapperComponent
  }, {
    path: 'update',
    component: UpdateAppointmentWrapperComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],


  exports: [RouterModule]
})
export class AddUpdateAppointmentRoutingModule {}
