import { Component, OnInit, ChangeDetectorRef, Input, ViewChild, AfterViewInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IMyOptions, MDBDatePickerComponent } from 'ng-uikit-pro-standard';

@Component({
  selector: 'app-date-input',
  templateUrl: './date-input.component.html',
  styleUrls: ['./date-input.component.scss']
})
export class DateInputComponent implements OnInit, AfterViewInit {
  public myDatePickerOptions: IMyOptions = {
    closeAfterSelect: true,
    // Strings and translations
    dayLabels: {
      su: 'Son',
      mo: 'Mon',
      tu: 'Die',
      we: 'Mit',
      th: 'Don',
      fr: 'Fre',
      sa: 'Sam'
    },
    dayLabelsFull: {
      su: 'Sonntag',
      mo: 'Montag',
      tu: 'Dienstag',
      we: 'Mittwoch',
      th: 'Donnerstag',
      fr: 'Freitag',
      sa: 'Samstag'
    },
    monthLabels: {
      1: 'Jan',
      2: 'Feb',
      3: 'Mär',
      4: 'Apr',
      5: 'Mai',
      6: 'Jun',
      7: 'Jul',
      8: 'Aug',
      9: 'Sep',
      10: 'Okt',
      11: 'Nov',
      12: 'Dez'
    },
    monthLabelsFull: {
      1: 'Januar',
      2: 'Februar',
      3: 'März',
      4: 'April',
      5: 'Mai',
      6: 'Juni',
      7: 'Juli',
      8: 'August',
      9: 'September',
      10: 'Oktober',
      11: 'November',
      12: 'Dezember'
    },

    // Buttons
    todayBtnTxt: 'Heute',
    clearBtnTxt: 'Löschen',
    closeBtnTxt: 'Schliessen',

    // Format
    dateFormat: 'dd.mm.yyyy',

    // First day of the week
    firstDayOfWeek: 'mo',

    // Disable dates
    disableWeekends: false,

    // Enable dates (when disabled) Year limits
    minYear: 1000,
    maxYear: 9999,

    // Show Today button
    showTodayBtn: true,

    // Show Clear date button
    showClearDateBtn: true,
  };
  @Input() isShift: boolean;
  @Input() dateGroup: FormGroup;
  @ViewChild('toDate') public toDate: MDBDatePickerComponent;

  constructor(private changeDetector: ChangeDetectorRef) { }

  ngOnInit() {
  }
  ngAfterViewInit(): void {
    console.log(this.toDate);
  }
  get deadLineString() {return this.dateGroup.get('deadLineString'); }
  get endDateString() {return this.dateGroup.get('endDateString'); }
  get startDateString() {return this.dateGroup.get('startDateString'); }
  get startTimeString() {return this.dateGroup.get('startTimeString'); }
  get endTimeString() {return this.dateGroup.get('endTimeString'); }
  fromDateInputFieldChanged(e) {
    console.log(e);
    this.changeDetector.detectChanges();
    console.log(this.toDate);
    this.toDate.openBtnClicked();
    console.log(this.dateGroup);
  }

}
