import { Component,
  OnInit,
  Output,
  EventEmitter,
  OnChanges,
  SimpleChanges,
  Input,
  ViewChild,
  ChangeDetectorRef,
  OnDestroy} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { AppointmentService } from 'src/app/core/services/appointment-service/appointment.service';
import { Subscription } from 'rxjs/internal/Subscription';
import { SelectComponent } from 'ng-uikit-pro-standard';

@Component({
  selector: 'app-type-input',
  templateUrl: './type-input.component.html',
  styleUrls: ['./type-input.component.scss']
})
export class TypeInputComponent implements OnInit, OnChanges, OnDestroy {
  allTypes: Array<{value: string, label: string}> = [];
  _allTypesSubscription: Subscription;
  @Output() typeEmitter = new EventEmitter<string>();
  @Input() typeGroup: FormGroup;
  @ViewChild('typeSelect') public typeSelect: SelectComponent;

  constructor(private appointmentService: AppointmentService,
    private changeDetector: ChangeDetectorRef) {
     }

  ngOnInit() {
    this.fetchTypes();
  }
  ngOnChanges(changes: SimpleChanges) {
    this.typeGroup = changes['typeGroup'].currentValue;
  }
  get type() {return this.typeGroup.get('type'); }
  fetchTypes() {
    this._allTypesSubscription = this
      .appointmentService
      .getTypes()
      .subscribe(data => {
        this.allTypes = [],
        data.forEach(element => {
          this
            .allTypes
            .push({value: element, label: element});
        });
      });
  }
  getType(e: {value: string, label: string}) {
   this.typeEmitter.emit(e.label);
  }
  ngOnDestroy(): void {
    this._allTypesSubscription.unsubscribe();
  }

}
