import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { CheckboxComponent } from 'ng-uikit-pro-standard';

@Component({
  selector: 'app-visibility-input',
  templateUrl: './visibility-input.component.html',
  styleUrls: ['./visibility-input.component.scss']
})
export class VisibilityInputComponent implements OnInit {
  @Input() visibilityGroup: FormGroup;
  @ViewChild('isVisible') public isVisible: CheckboxComponent;
  @ViewChild('showParticipants') public showParticipants: CheckboxComponent;

  constructor() { }

  ngOnInit() {
  }
  checkVisibleState() {
    if (!this.visibilityGroup.get('isVisible').value && this.visibilityGroup.get('participantsVisible').value) {
      this.visibilityGroup.patchValue({participantsVisible: false});
    }
    return this.visibilityGroup.get('isVisible').value;
  }

}
