import { Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  OnChanges,
  SimpleChanges,
  AfterViewInit,
  ChangeDetectorRef,
  ViewChild,
  OnDestroy} from '@angular/core';
import { FormGroup} from '@angular/forms';
import * as _ from 'lodash';
import { SelectComponent } from 'ng-uikit-pro-standard';
import { Organisation } from 'src/app/shared/models/organisation';
import { User } from 'src/app/shared/models/user';
import { Roles } from 'src/app/shared/enums/static-strings';

@Component({
  selector: 'app-participant-input',
  templateUrl: './participant-input.component.html',
  styleUrls: ['./participant-input.component.scss']
})
export class ParticipantInputComponent implements OnInit, OnChanges, AfterViewInit, OnDestroy {
  @Input() isShift: boolean;
  @Input() participantGroup: FormGroup;
  @Output() participantsAreOrgs = new EventEmitter<boolean>();
  @Input() allUsers: Array<User>;
  @Input() allOrganisations: Array<Organisation>;
  @ViewChild('optionSelect') public optionSelect: SelectComponent;
  @ViewChild('userSelect') public userSelect: SelectComponent;
  @ViewChild('organisationSelect') public organisationSelect: SelectComponent;
  showOrganisation = false;
  showUser = false;
  allUsersMultiSelect: Array < {value: number, label: string} > = [];
  allOrganisationsMultiSelect: Array < {value: number, label: string} > = [];
  participantOptions: Array < {value: number, label: string} > = [
    {
      value: 1,
      label: 'Organisationen'
    }, {
      value: 2,
      label: 'Mitglieder'
    }
  ];

  constructor(private changeDetector: ChangeDetectorRef) { }

  ngOnInit() {
    if (this.isShift) {
      this.participantGroup.patchValue({maxParticipants: 1});
      this.showUser = true;
      console.log(this.allUsers);
      this.pushUsersToMultiSelect();
    }
  }
  ngAfterViewInit(): void {
  }
  ngOnChanges(changes: SimpleChanges) {
    if (!!changes['allUsers']) {
      this.allUsers = changes['allUsers'].currentValue;
      if (this.showUser) {
        this.pushUsersToMultiSelect();
      }
    }
    if (!!changes['allOrganisations']) {
      console.log(this.participantGroup.value);
      console.log(this.participantGroup.get('participants').value);
      this.allOrganisations = changes['allOrganisations'].currentValue;
      if (this.showOrganisation) {
        this.pushOrganisationToMultiSelect();
      }
    }
    if (!!changes['participantGroup']) {
      this.participantGroup = changes['participantGroup'].currentValue;
      console.log(this.participantGroup.value);
      if (this.participantGroup.get('areParticipantsOrgs').value === 1) {
        this.selectedOption(this.participantOptions[0]);
      } else if (this.participantGroup.get('areParticipantsOrgs').value === 2) {
        this.selectedOption(this.participantOptions[1]);
      }

    }
  }
  get maxParticipants() {
    return this.participantGroup.get('maxParticipants');
  }
  get participants() {
    return this.participantGroup.get('participants');
  }
  get areParticipantsOrgs() {return this.participantGroup.get('areParticipantsOrgs'); }
  selectedOption(e) {
    if (e.label === 'Organisationen') {
      this.pushOrganisationToMultiSelect();
      this.showOrganisation = true;
      this.showUser = false;
    } else if (e.label === 'Mitglieder') {
      this.pushUsersToMultiSelect();
      this.showOrganisation = false;
      this.showUser = true;
    }
  }
  pushOrganisationToMultiSelect() {
    this.allOrganisationsMultiSelect = [];
    this
      .allOrganisations
      .forEach(organisation => {
        this
          .allOrganisationsMultiSelect
          .push({value: organisation.id, label: organisation.name});
      });
  }
  pushUsersToMultiSelect() {
    this.allUsersMultiSelect = [];
    this
      .allUsers
      .forEach(user => {
        if (this.isShift) {
          if (_.includes(user.entitlements, Roles[Roles.UserBwst]) || _.includes(user.entitlements, Roles[Roles.AdminBwst])) {
            this
              .allUsersMultiSelect
              .push({
                value: user.id,
                label: user.firstname + ' ' + user.lastname,
              });
          }

        } else {
          this
            .allUsersMultiSelect
            .push({
              value: user.id,
              label: user.firstname + ' ' + user.lastname
            });
        }

      });
      console.log(this.allUsersMultiSelect);
    this.showUser = true;

  }
  ngOnDestroy(): void {
  }

}
