import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { ToastService } from 'ng-uikit-pro-standard';
import * as moment from 'moment';
import { ActivatedRoute } from '@angular/router';
import { AppointmentService } from 'src/app/core/services/appointment-service/appointment.service';
import { SessionUserService } from 'src/app/core/services/session-user-service/session-user.service';
import { participantsRequired,
  participantsVal,
  dateValidation,
  appointmentTimeValidation } from 'src/app/shared/validation/customValidations';
import { AppointmentTypes } from 'src/app/shared/enums/static-strings';
import { User } from 'src/app/shared/models/user';
import { Organisation } from 'src/app/shared/models/organisation';
import { Appointment } from 'src/app/shared/models/appointment';
import { OrganisationService } from 'src/app/core/services/organisation-service/organisation.service';
import { UserService } from 'src/app/core/services/user-service/user.service';
import { RolesService } from 'src/app/core/services/roles-service/roles.service';
import { Subscription } from 'rxjs/internal/Subscription';

@Component({
  selector: 'app-add-appointment-wrapper',
  templateUrl: './add-appointment-wrapper.component.html',
  styleUrls: ['./add-appointment-wrapper.component.scss']
})
export class AddAppointmentWrapperComponent implements OnInit, OnDestroy {
  addForm: FormGroup;
  _routeSubscription: Subscription;
  _allUsersSubscription: Subscription;
  _allOrgsSubscription: Subscription;
  _areParticipantsOrgsSubscription: Subscription;
  allUsers: Array < User > = [];
  allOrganisations: Array<Organisation> = [];
  activeView = 0;
  isShift = false;
  views: Array<boolean> = [
    true,
    false,
    false,
    false,
    false];
  constructor(private fb: FormBuilder,
    private toast: ToastService,
    private appointmentService: AppointmentService,
    private sessionUser: SessionUserService,
    private route: ActivatedRoute,
    private organisationService: OrganisationService,
    private userService: UserService,
    private roleService: RolesService) {
    this.addForm = fb.group({
      'type': ['', [Validators.required]],
      'title': ['', [Validators.required, Validators.maxLength(30)]],
      'description': ['', [Validators.maxLength(300)]],
      'participants': ['', [Validators.required]],
      'areParticipantsOrgs': new FormControl('', {validators: Validators.required, updateOn: 'change'}),
      'maxParticipants': [
        ' ',
        [Validators.max(60)]
      ],
      'deadLineString': [''],
      'endDateString': [
        '', Validators.required
      ],
      'startDateString': [
        '', Validators.required
      ],
      'startTimeString': [
        '',
        [Validators.required]
      ],
      'endTimeString': [
        '',
        [Validators.required]
      ],
      'isVisible': new FormControl('', {updateOn: 'change'}),
      'participantsVisible': new FormControl('', {updateOn: 'change'})
    }, {
      updateOn: 'submit',  validators: Validators.compose([participantsRequired, participantsVal, dateValidation])
    });
  }
  ngOnInit() {
    if (this.roleService.isUserBfk) {
      this.addForm.setValidators(Validators.compose([participantsRequired, participantsVal, dateValidation, appointmentTimeValidation]));
    }
    this.fetchOrgs();
    this.fetchUsers();
    this._routeSubscription = this.route.queryParams.subscribe(queryparams => {
      if (!!queryparams) {
        this.addForm.patchValue({startDateString: queryparams['date']});
      }
    });
    this.addForm.patchValue({isVisible: false, participantsVisible: false});
  }
  fetchOrgs() {
    this._allOrgsSubscription = this.organisationService._allOrganisations.subscribe((orgs: Organisation[]) => {
      this.allOrganisations = [];
      this.allOrganisations = orgs;
    });
  }
  fetchUsers() {
    this._allUsersSubscription = this.userService._allUsers.subscribe((users: User[]) => {
      this.allUsers = [];
      this.allUsers = users;
    });
  }
  getType(event: string) {
    if (event === AppointmentTypes[AppointmentTypes.Dienst] ||
      event === AppointmentTypes[AppointmentTypes.MarktplatzDienst] ||
      event === AppointmentTypes[AppointmentTypes.FreierDienst] ) {
        this.isShift = true;
        this.addForm.patchValue({areParticipantsOrgs: false, participants: []});
      } else {
        this.isShift = false;
      }
  }
  areParticipantsOrgsChangeEvent() {
    this._areParticipantsOrgsSubscription = this.addForm.get('areParticipantsOrgs').valueChanges.subscribe(data => {
      this.addForm.patchValue({participants: []});
    });
  }
  stepBack(): void {
    this.views[this.activeView] = false;
    this.activeView -= 1;
    this.views[this.activeView] = true;
  }
  nextView(): void {
    this.views[this.activeView] = false;
    this.activeView += 1;
    this.views[this.activeView] = true;
  }
  stepForward(): void {
    this.activeView = this.views.findIndex(val => val === true);
    console.log(this.addForm);
    switch (this.activeView) {
      case 0:
        if (this.addForm.get('type').valid) {
          this.addForm.get('description').reset();
          this.nextView();
        }
        break;
      case 1:
      console.log(this.addForm);
      if (this.addForm.get('title').valid && this.addForm.get('description').valid) {
        this.nextView();
        this.areParticipantsOrgsChangeEvent();
      }
      break;
      case 2:
      console.log(this.addForm);
        if (this.addForm.get('participants').valid
        && this.addForm.get('maxParticipants').valid
        && this.addForm.get('areParticipantsOrgs').valid) {
          this.nextView();
        }
        break;
      case 3:
        if (this.addForm.hasError('onlyNightShiftsDuringWeek')) {
          this.toast.error('Zwischen Mo-Fr nur Nachtschichten zwischen 18-6 Uhr');
        }
        if (this.addForm.get('startTimeString').hasError('required') ||
        this.addForm.get('endTimeString').hasError('required')) {
          this
            .toast
            .error('StartZeit und EndZeit sind verpflichtend');
        }
        if (this.addForm.get('endDateString').valid
        && this.addForm.get('startDateString').valid
        && this.addForm.get('startTimeString').valid
        && this.addForm.get('endTimeString').valid) {
          this.nextView();
        }

        break;
        case 4:
        const copyParticipants: Array<User|Organisation> = [];
        if (this.addForm.get('areParticipantsOrgs').value === 1) {
          this.addForm.get('participants').value.forEach(id => {
            copyParticipants.push(this.allOrganisations.find(org => org.id === id));
          });
          this.addForm.patchValue({areParticipantsOrgs: true});
        } else {
          this.addForm.get('participants').value.forEach(id => {
            copyParticipants.push(this.allUsers.find(user => user.id === id));
          });
          this.addForm.patchValue({areParticipantsOrgs: false});
        }
        this.addForm.patchValue({'participants': copyParticipants});
        let owner: User = this.sessionUser.getUser();
        let deadline: Date = moment(this.addForm.get('deadLineString').value, 'DD.MM.YYYY').toDate();
        if (this.isShift) {
          owner = this.addForm.get('participants').value[0];
          this.addForm.patchValue({participants : []});
          deadline = null;
        }
        // tslint:disable-next-line:max-line-length
        const startDate: Date = moment(this.addForm.get('startDateString').value + ' ' + this.addForm.get('startTimeString').value, 'DD.MM.YYYY HH:mm').toDate();
        // tslint:disable-next-line:max-line-length
        const endDate: Date = moment(this.addForm.get('endDateString').value + ' ' + this.addForm.get('endTimeString').value, 'DD.MM.YYYY HH:mm').toDate();
          const appointmentToAdd: Appointment = new Appointment(99999,
            this.addForm.get('title').value,
            this.addForm.get('description').value,
            startDate,
            endDate,
            this.addForm.get('participants').value,
            this.addForm.get('maxParticipants').value,
            deadline,
            this.addForm.get('participantsVisible').value,
            owner,
            this.addForm.get('type').value,
            this.addForm.get('isVisible').value,
            this.addForm.get('areParticipantsOrgs').value);
            this.appointmentService._appointmentToAdd.next(appointmentToAdd);
        break;
      default:
        break;
    }
  }

  ngOnDestroy(): void {
    this._routeSubscription.unsubscribe();
    this._allOrgsSubscription.unsubscribe();
    this._allUsersSubscription.unsubscribe();
    if (this._areParticipantsOrgsSubscription != null) {
      this._areParticipantsOrgsSubscription.unsubscribe();
    }
  }
}
