import {
  Component,
  OnInit,
  OnDestroy,
} from '@angular/core';
import {ToastService} from 'ng-uikit-pro-standard';
import * as _ from 'lodash';
import * as moment from 'moment';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {ActivatedRoute} from '@angular/router';
import { Appointment } from 'src/app/shared/models/appointment';
import { AppointmentTypes } from 'src/app/shared/enums/static-strings';
import { SessionUserService } from 'src/app/core/services/session-user-service/session-user.service';
import { AppointmentService } from 'src/app/core/services/appointment-service/appointment.service';
import { participantsRequired,
  participantsVal,
  dateValidation,
  appointmentTimeValidation } from 'src/app/shared/validation/customValidations';
import { User } from 'src/app/shared/models/user';
import { Organisation } from 'src/app/shared/models/organisation';
import { UserService } from 'src/app/core/services/user-service/user.service';
import { OrganisationService } from 'src/app/core/services/organisation-service/organisation.service';
import { RolesService } from 'src/app/core/services/roles-service/roles.service';
import { Subscription } from 'rxjs/internal/Subscription';

// tslint:disable-next-line:max-line-length
@Component({selector: 'app-update-appointment-wrapper', templateUrl: './update-appointment-wrapper.component.html', styleUrls: ['./update-appointment-wrapper.component.scss']})
export class UpdateAppointmentWrapperComponent implements OnInit,
OnDestroy {

  updateForm: FormGroup;
  _routeSubscription: Subscription;
  _allAppointemntSubscription: Subscription;
  _allUsersSubscription: Subscription;
  _allOrgsSubscription: Subscription;
  _areParticipantsOrgsSubscription: Subscription;
  allUsers: Array < User > = [];
  allOrganisations: Array<Organisation> = [];
  activeView = 0;
  titleError = ' ';
  isShift = false;
  descriptionError = ' ';
  appointmentToUpdate: Appointment;
  id: number;
  type: string;
  public appTypes: typeof AppointmentTypes = AppointmentTypes;

  constructor(private toast: ToastService,
    private fb: FormBuilder,
    private sessionUser: SessionUserService,
    private route: ActivatedRoute,
    private appointmentService: AppointmentService,
    private userService: UserService,
    private organisationService: OrganisationService,
    private roleService: RolesService) {
      this.updateForm = fb.group({
        'type': ['', [Validators.required]],
        'title': ['', [Validators.required, Validators.maxLength(30)]],
        'description': ['', [Validators.maxLength(300)]],
        'participants': ['', [Validators.required]],
        'areParticipantsOrgs': new FormControl('', {validators: Validators.required, updateOn: 'change'}),
        'maxParticipants': [
          ' ',
          [Validators.max(60)]
        ],
        'deadLineString': [''],
        'endDateString': [
          '', Validators.required
        ],
        'startDateString': [
          '', Validators.required
        ],
        'startTimeString': [
          '',
          [Validators.required]
        ],
        'endTimeString': [
          '',
          [Validators.required]
        ],
        'isVisible': new FormControl('', {updateOn: 'change'}),
        'participantsVisible': new FormControl('', {updateOn: 'change'})
      }, {
        updateOn: 'submit',  validators: Validators.compose([participantsRequired, participantsVal, dateValidation])
      });

  }
  ngOnInit() {
    this.fetchOrgs();
    this.fetchUsers();
    if (this.roleService.isUserBfk) {
      this.updateForm.setValidators(Validators.compose([participantsRequired, participantsVal, dateValidation, appointmentTimeValidation]));
    }
    this._routeSubscription = this.route.queryParams.subscribe(queryparams => {
      if (!!queryparams) {
        const appointmentToUpdateId: number = JSON.parse(queryparams.AppointmentToUpdate);
        console.log(appointmentToUpdateId);
              this._allAppointemntSubscription = this.appointmentService._allAppointments.subscribe(appointments => {
                if (appointments.length > 0) {
                  console.log(appointments);
                this.appointmentToUpdate = appointments.find((appointment) => appointmentToUpdateId === appointment.id);
                console.log(this.appointmentToUpdate);
                this.checkVals();
                }
              });
      }
    });
  }
  checkVals(): void {
      this.id = this.appointmentToUpdate.id;
      this.type = this.appointmentToUpdate.type;
      console.log(this.updateForm);
      if (this.type === AppointmentTypes[AppointmentTypes.Dienst] ||
        this.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst] ||
        this.type === AppointmentTypes[AppointmentTypes.FreierDienst]) {
            this.updateForm.patchValue({participants : [this.appointmentToUpdate.owner.id]});
            this.isShift = true;
        } else {
          this.isShift = false;
          const selectedParticipants: Array<number> = [];
          this.appointmentToUpdate.participations.forEach(participant => {
            selectedParticipants.push(participant.id);
          });
          this.updateForm.patchValue({participants: selectedParticipants});
        }
      this.updateForm.patchValue({type: this.appointmentToUpdate.type});
      if (this.appointmentToUpdate.areParticipantsOrganisations) {
        this.updateForm.patchValue({areParticipantsOrgs: 1});
      } else {
        this.updateForm.patchValue({areParticipantsOrgs: 2});
      }
      this.areParticipantsOrgsChangeEvent();
      this.updateForm.patchValue({isVisible: this.appointmentToUpdate.isVisible,
        participantsVisible: this.appointmentToUpdate.showParticipants});
      if (this.appointmentToUpdate.deadline != null) {
        this
          .updateForm
          .patchValue({
            deadLineString: moment(this.appointmentToUpdate.deadline)
              .format('DD.MM.YYYY')
              .toString()
          });
      }
      this.updateForm.patchValue({startDateString: moment(this.appointmentToUpdate.from)
        .format('DD.MM.YYYY')
        .toString(),
      endDateString: moment(this.appointmentToUpdate.to)
        .format('DD.MM.YYYY')
        .toString(),
      startTimeString: moment(this.appointmentToUpdate.from)
        .format('HH:mm')
        .toString(),
      endTimeString: moment(this.appointmentToUpdate.to)
        .format('HH:mm')
        .toString()});
      this.updateForm.patchValue({maxParticipants: this.appointmentToUpdate.maxParticipants});
      this
        .updateForm
        .patchValue({title: this.appointmentToUpdate.title,
          description: this.appointmentToUpdate.description});

  }
  areParticipantsOrgsChangeEvent() {
    this._areParticipantsOrgsSubscription = this.updateForm.get('areParticipantsOrgs').valueChanges.subscribe(data => {
      this.updateForm.patchValue({participants: []});
    });
  }
  fetchOrgs() {
    this._allOrgsSubscription = this.organisationService._allOrganisations.subscribe((orgs: Organisation[]) => {
      this.allOrganisations = [];
      this.allOrganisations = orgs;
    });
  }
  fetchUsers() {
    this._allUsersSubscription = this.userService._allUsers.subscribe((users: User[]) => {
      this.allUsers = [];
      this.allUsers = users;
    });
  }

  onSubmit() {
        if (this.updateForm.hasError('onlyNightShiftsDuringWeek')) {
          this.toast.error('Zwischen Mo-Fr nur Nachtschichten zwischen 18-6 Uhr');
        }
        if (this.updateForm.get('startTimeString').hasError('required') ||
        this.updateForm.get('endTimeString').hasError('required')) {
          this
            .toast
            .error('StartZeit und EndZeit sind verpflichtend');
        }
        if (this.updateForm.valid) {
          const copyParticipants: Array<User|Organisation> = [];
          if (this.updateForm.get('areParticipantsOrgs').value === 1) {
            this.updateForm.get('participants').value.forEach(id => {
              copyParticipants.push(this.allOrganisations.find(org => org.id === id));
            });
            this.updateForm.patchValue({areParticipantsOrgs: true});
          } else {
            this.updateForm.get('participants').value.forEach(id => {
              copyParticipants.push(this.allUsers.find(user => user.id === id));
            });
            this.updateForm.patchValue({areParticipantsOrgs: false});
          }
          this.updateForm.patchValue({participants: copyParticipants});
          let owner: User = this.sessionUser.getUser();
        let deadline: Date = moment(this.updateForm.get('deadLineString').value, 'DD.MM.YYYY').toDate();
        if (this.isShift) {
          owner = this.updateForm.get('participants').value[0];
          this.updateForm.patchValue({participants : []});
          deadline = null;
        }
        // tslint:disable-next-line:max-line-length
        const startDate: Date = moment(this.updateForm.get('startDateString').value + ' ' + this.updateForm.get('startTimeString').value, 'DD.MM.YYYY HH:mm').toDate();
        // tslint:disable-next-line:max-line-length
        const endDate: Date = moment(this.updateForm.get('endDateString').value + ' ' + this.updateForm.get('endTimeString').value, 'DD.MM.YYYY HH:mm').toDate();
          const appointmentToUpdate: Appointment = new Appointment(this.id,
            this.updateForm.get('title').value,
            this.updateForm.get('description').value,
            startDate,
            endDate,
            this.updateForm.get('participants').value,
            this.updateForm.get('maxParticipants').value,
            deadline,
            this.updateForm.get('participantsVisible').value,
            owner,
            this.type,
            this.updateForm.get('isVisible').value,
            this.updateForm.get('areParticipantsOrgs').value);
            this.appointmentService.appointmentToUpdate.next(appointmentToUpdate);
        }
  }
  ngOnDestroy(): void {
    this._routeSubscription.unsubscribe();
    this._allOrgsSubscription.unsubscribe();
    this._allUsersSubscription.unsubscribe();
    this._areParticipantsOrgsSubscription.unsubscribe();
  }
}
